body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.TodoCounter {
  background-color: #01110a;
  color: #fff7f8;
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  font-weight: 300;
  padding: 10px 0;
  text-align: center;
  width: 100vw;
  margin-bottom: 12px;
}

.inputContainer {
  margin: 12px 0;
}

.inputContainer > input {
  font-size: 14px;
  padding: 4px 10px;
  border-radius: 4px;
  width: 70vw;
}

.inputContainer > p {
  color: white;
}

section {
  margin: 24px 0;
}
ul {
  list-style-type: none;
  width: 75vw;
}

li {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: white;
  padding: 10px 0;
  margin: 2px 0;
}

.todoItem {
  font-family: "Roboto", sans-serif;
  /* text-decoration: line-through; */
}

.tasks-buttons {
  cursor: pointer;
}

.CreateTodoButton {
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
  z-index: 1;
}

.CreateTodoButton:focus {
  -webkit-animation: addToClose 1000ms;
          animation: addToClose 1000ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes addToClose {
  0% {
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes addToClose {
  0% {
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.ModalBackground {
  background: rgba(32, 35, 41, 0.8);
  position: fixed;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

form,
.buttonContainer {
  display: flex;
  font-family: Roboto, Oxygen, "Helvetica Neue", sans-serif;
}
form {
  flex-direction: column;
}
form > textarea {
  min-width: 24rem;
  font-family: Roboto, Oxygen, "Helvetica Neue", sans-serif;
  padding: 0.8rem 0.4rem 0 0.4rem;
  font-size: 1rem;
}
.buttonContainer {
  margin-top: 1.2rem;
  justify-content: space-around;
}

.buttonContainer > button {
  min-width: 6rem;
  padding: 0.5rem;
  border: none;
  border-radius: 0.2rem;
  cursor: pointer;
}
.submitButton {
  background-color: rgba(27, 153, 139, 0.5);
  color: white;
  font-weight: 500;
}

.submitButton:hover {
  background: rgba(27, 153, 139, 1);
}

.submitButton:focus {
  background: rgba(89, 255, 160, 0.5);
}
.cancelButton {
  background-color: white;
  color: black;
  font-weight: 500;
}

.cancelButton:hover {
  background: rgba(89, 255, 160, 0.5);
  color: white;
}

.cancelButton:focus {
  background: rgba(89, 255, 160, 1);
  color: black;
}

.loadingTask {
  align-items: center;
  color: white;
  display: flex;
  flex-direction: row;
  margin: 0.3rem 0;
}

.loadingCheck {
  border-radius: 50%;
  border: 2px solid white;
  height: 1.2rem;
  margin-right: 1.5rem;
  opacity: 50%;
  width: 1.2rem;
}

.loadingTaskText {
  align-items: center;
  -webkit-animation: loadingMovement 800ms linear infinite;
          animation: loadingMovement 800ms linear infinite;
  border-radius: 4px;
  display: flex;
  height: 1.8rem;
  padding-left: 0.5rem;
  width: 80vw;
}

@-webkit-keyframes loadingMovement {
  0% {
    background: linear-gradient(
      45deg,
      rgba(27, 153, 139, 1) 0%,
      rgba(89, 255, 160, 0.2) 100%
    );
  }

  60% {
    background: linear-gradient(
      45deg,
      rgba(27, 153, 139, 0.3) 20%,
      rgba(89, 255, 160, 0.8) 80%
    );
  }

  100% {
    background: linear-gradient(
      45deg,
      rgba(27, 153, 139, 0.2) 0%,
      rgba(89, 255, 160, 1) 100%
    );
  }
}

@keyframes loadingMovement {
  0% {
    background: linear-gradient(
      45deg,
      rgba(27, 153, 139, 1) 0%,
      rgba(89, 255, 160, 0.2) 100%
    );
  }

  60% {
    background: linear-gradient(
      45deg,
      rgba(27, 153, 139, 0.3) 20%,
      rgba(89, 255, 160, 0.8) 80%
    );
  }

  100% {
    background: linear-gradient(
      45deg,
      rgba(27, 153, 139, 0.2) 0%,
      rgba(89, 255, 160, 1) 100%
    );
  }
}

.errorContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
}
.errorIcon {
  display: flex;
  background-color: #991a29;
  border-radius: 50%;
  height: 2rem;
  width: 2rem;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 600;
  font-size: 1.5rem;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  border: 0.2rem solid white;
  box-sizing: content-box;
  margin-bottom: 1.5rem;
}
.errorMsg {
  color: white;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
header {
  align-items: center;
  background: linear-gradient(
    45deg,
    rgba(27, 153, 139, 0.7) 0%,
    rgba(89, 255, 160, 1) 100%
  );
  display: flex;
  justify-content: center;
  height: 18vh;
}

main {
  height: 82vh;
  background: #181824;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  color: #01110a;
}

p {
  color: white;
}

